
.container {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    padding: 1px;
    border: 1px solid rgb(167, 172, 167);
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  .h3{
    color: #283618;
    font-size: 35px;
    border-bottom: 5px solid #d90429;
  }
  ul>li{
    font-size: 18px;
    
  }
  
  .brand {
    text-align: center;
      font-weight: 300;
      text-transform: uppercase;
      letter-spacing: 0.1em;
  }
  
  /* ------------------- */
  /* COMPANY INFORMATION */
  /* ------------------- */
  
  .company-info {
    background: #ced4da;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-right: 1px solid rgb(161, 160, 160);
  }
  .company-info:hover {
    background: #84a59d;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  
  .company-info h3,
  .company-info ul {
    text-align: center;
    margin: 0 0 1rem 0;
  }
  
  /* ------- */
  /* CONTACT */
  /* ------- */
  
  .contact {
    background: #ced4da;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  .contact:hover {
    background: #84a59d;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  
  /* ---- */
  /* FORM */
  /* ---- */
  
  .contact form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
  
  .contact form label {
    display: block;
  }
  
  .contact form p {
    margin: 0;
  }
  
  .contact form .full {
    grid-column: 1 / 3;
  }
  
  .contact form button,
  .contact form input,
  .contact form textarea {
    width: 100%;
    padding: 1em;
    border: solid 2px #112764;
    border-radius: 4px;
  }
  
  .contact form textarea {
    resize: none;
  }
  
  .contact form button {
    background: #006d77;
    border: 0;
    width: 180px;
    margin-left: auto;
    color: #e4e4e4;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
  }
  
  .contact form button:hover,
  .contact form button:focus {
    background: #363d53;
    color: #ffffff;
    outline: 0;
    transition: background-color 1s ease-out;
  }
  
  /* ------------- */
  /* MEDIA QUERIES */
  /* ------------- */
  
  @media only screen and (min-width: 700px) {
    .wrapper {
      display: grid;
      grid-template-columns: 1fr 2fr;
    }
  
    .wrapper > * {
      padding: 2em;
    }
  
    .company-info {
      border-radius: 4px 0 0 4px;
    }

    
@keyframes slide {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

.h3 {  
  /* This section calls the slideInFromLeft animation we defined above */
  animation: 1s ease-out 0s 1 slide;
  }

  
    .contact {
      border-radius: 0 4px 4px 0;
      
    }
  
    .company-info h3,
    .company-info ul,
    .brand {
      text-align: left;
    }
  }


  