:root {
  --landscape-width: 19;
  --portrait-width: 90vw;
  --header-height: 2.5rem;
  --secs: 0.5s;
  --bg-left: springgreen;
  --bg-right: cyan;
  --bg-main: brown;
}
#layout {
  display: flex;
  overflow: hidden;
}
#layout .header {
  position: relative;
  height: 2.5rem;
  height: var(--header-height);
  text-align: center;
}
#layout .header .title {
  position: absolute;
  left: 0;
  right: 0;
}

