/* ------------------- */
/* COMPANY INFORMATION */
/* ------------------- */

.company-info {
  background: #ced4da;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-right: 1px solid rgb(161, 160, 160);
}
.company-info:hover {
  background: #84a59d;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.company-info h3,
.company-info ul {
  text-align: center;
  margin: 0 0 1rem 0;
}

/* ------- */
/* CONTACT */
/* ------- */

.contact {
  background: #ced4da;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.contact:hover {
  background: #84a59d;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

/* ---- */
/* FORM */
/* ---- */

.contact form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.contact form label {
  display: block;
}

.contact form p {
  margin: 0;
}

.contact form .full {
  grid-column: 1 / 3;
}

.contact form button,
.contact form input,
.contact form textarea {
  width: 100%;
  padding: 1em;
  border: solid 2px #112764;
  border-radius: 4px;
}

.contact form textarea {
  resize: none;
}

.contact form button {
  background: #006d77;
  border: 0;
  width: 180px;
  margin-left: auto;
  color: #e4e4e4;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
}

.contact form button:hover,
.contact form button:focus {
  background: #363d53;
  color: #ffffff;
  outline: 0;
  transition: background-color 1s ease-out;
}

